import Vue from "vue";
import Router from "vue-router";
import Home from "./components/Home";
import Register from "./components/Register";
import Menu from "./components/Menu";
import Login from "./components/Login";
import Header from "./components/Header";
import Admin from "./components/Admin";

// 二级路由
import Contact from "./components/about/Contact";
import Delivery from "./components/about/Delivery";
import History from "./components/about/History";
import OrderingGuide from "./components/about/OrderingGuide";

// 三级路由
import I from "./components/about/iphone/I";
import My from "./components/about/iphone/My";
Vue.use(Router);

export default new Router({
    routes: [
        {
            path: "/",
            name: "home",
            components: {
                default:Home,
                "orderingGuide":OrderingGuide,
                "delivery":Delivery,
                "history":History,
            },
        },
        {
            path: "/register",
            name: "register",
            component: Register,
        },
        {
            path: "/menu",
            name: "menu",
            component: Menu,
        },
        {
            path: "/login",
            name: "login",
            component: Login,
        },
        {
            path: "/header",
            name: "header",
            component: Header,
        },
        {
            path: "/admin",
            name: "admin",
            component: Admin,
            // // 路由独享守卫
            // beforeEnter(to,from,next){
            //     alert("未登录,滚");
            //     next(false);
            // },
        },
        {
            path: "/about",
            name: "about",
            redirect:"/about/contact",
            component: () =>//懒加载路由
                import(/* webpackChunkName: "about" */ "./components/about/About.vue"),
            children:[
                {path:"/about/contact",name:"contactLink",component:Contact,children:[
                    {path:"/about/contact/i",name:"i",component:I},
                    {path:"/about/contact/my",name:"my",component:My},
                ],
                redirect:"/about/contact/i",
                },
                {path:"/about/delivery",name:"deliveryLink",component:Delivery},
                {path:"/about/history",name:"historyLink",component:History},
                {path:"/about/ordering",name:"orderingGuideLink",component:OrderingGuide},
            ],
        },
        {//输错路由地址
            path:"*",
            name:"error",
            redirect:"/",
        },
    ],
    mode:"history",//去掉#
    // scrollBehavior(to,from,savedPosition){
    //     if(savedPosition){
    //         return savedPosition;
    //     }else{
    //         return {x:0,y:0};
    //     }
    // },
});
